import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/layout'

Vue.use(Router)

const routes = [
    {
        path: '/',
        component: Layout,
        children: [
            {
                path: '/',
                name:'basic',
                component: () => import('@/views/BasicData/index'),
                meta: { title: '基础数据' },
                redirect: '/city',
                children: [
                    {
                        path: 'city',
                        name:'city',
                        meta: { title: '主要城市' },
                        component: () => import('@/views/BasicData/CityGDP')
                    }
                ]
            },
            {
                path: 'economically',
                name:'economically',
                meta: { title: '经营相关数据' },
                component: () => import('@/views/Economically')
            },
            {
                path: 'operation',
                name:'operation',
                meta: { title: '运营相关数据' },
                component: () => import('@/views/Operation'),
                redirect: '/operation/list1/num1',
                children: [
                    {
                        path: 'list1',
                        name:'list1',
                        component: () => import('@/views/Operation/List1'),
                        redirect: '/operation/list1/num1',
                        children: [
                            {
                                path: 'num1',
                                name:'num1',
                                meta: { title: '数据1' },
                                component: () => import('@/views/Operation/List1/Num1')
                            },
                            {
                                path: 'num2',
                                name:'num2',
                                meta: { title: '数据2' },
                                component: () => import('@/views/Operation/List1/Num2')
                            },
                        ]
                    },
                    {
                        path: 'list2',
                        name:'list2',
                        meta: { title: '运行2' },
                        component: () => import('@/views/Operation/List2')
                    },
                    {
                        path: 'list3',
                        name:'list3',
                        meta: { title: '运行3' },
                        component: () => import('@/views/Operation/List3')
                    },
                    {
                        path: 'list4',
                        name:'list4',
                        meta: { title: '运行4' },
                        component: () => import('@/views/Operation/List4')
                    },
                ]
            },
            {
                path: 'resource',
                name:'resource',
                meta: { title: '资源数据' },
                component: () => import('@/views/Resource')
            },
            {
                path: 'networkstructure',
                name:'networkstructure',
                meta: { title: '网络结构数据' },
                component: () => import('@/views/NetworkStructure')
            },
            {
                path: 'airLinedecision',
                name:'airLinedecision',
                meta: { title: '航线决策支持数据' },
                component: () => import('@/views/AirLineDecision')
            },
            {
                path: 'networkknowledge',
                name:'networkknowledge',
                meta: { title: '网络知识库' },
                component: () => import('@/views/NetworkKnowledge')
            }
        ]
    }
]

const router = new Router({
    routes
})

export default router