            <el-dropdown-item>网络知识库数据信息</el-dropdown-item>
            <el-dropdown-item>经营相关数据信息</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <!-- <span class="message"><img src="../../assets/message1.png" alt=""></span> -->
        <span :plain="true" @click="open">收藏</span>
        <el-dropdown>
          <span class="box">
            超级管理员
            <img src="../../assets/user.jpeg" alt="">
          </span>

          <!-- <span class="user">
            {{user}}
            <i class="el-icon-caret-bottom"></i>
          </span> -->
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>test1</el-dropdown-item>
            <el-dropdown-item>test2</el-dropdown-item>
            <el-dropdown-item>test3</el-dropdown-item>
            <el-dropdown-item divided @click.native='logout'>退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
