    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: 'red'
    }
  },
  components: {

  },
  methods: {
    //折叠导航栏
    collapse: function() {
      // this.isCollapse = !this.isCollapse;
      this.$store.commit("collapse");
    },
    logout: function() {
      this.$confirm("确认退出嘛？","提示",{
        type: "warning"
      })
        .then(() => {
          sessionStorage.removeItem("user");
          // this.$router.push("/login");
          this.$router.push("/");
        })
        .catch(() => {});
    },
    open() {
      this.$message({
        message: '收藏成功！',
        type: 'success',
        duration: 2000,
       
      });
    }
  },
  mounted() {
    this.user = sessionStorage.getItem("user");
  }
};
</script>

